@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.wrapper {
  -webkit-scroll-snap-type: y mandatory;
      -ms-scroll-snap-type: y mandatory;
          scroll-snap-type: y mandatory;
}

.smtext {
  font-weight: 700;
  font-size: 11px;
  letter-spacing: 2px;
  font-family: "Montserrat";
  margin-top: 0.7rem;
}

#canvas1 {
  position: fixed;
  z-index: -1;
  width: 99.4vw;
  height: 99.4vh;
}

.texts {
  position: absolute;
  top: 48%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.dash {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.proxytexts {
  display: flex;
  justify-content: space-between;
}

.header {
  scroll-snap-align: start;
  text-align: center;
  font-size: 80px;
  margin: 3rem;
  font-weight: 700;
}

.smheader {
  font-family: "Montserrat";
  letter-spacing: 3px;
  text-align: center;
}

button {
  background: none;
  border: none;
}

.remaining {
  font-size: 35px;
  font-weight: 700;
}

.proxytext {
  font-size: 23px;
  font-weight: 700;
}

* {
  outline: none;
}

a:hover {
  -webkit-filter: brightness(0.8);
          filter: brightness(0.8);
}

.proxytext span {
  margin-right: 0.5rem;
}

.maindash {
  display: grid;
  grid-template-columns: -webkit-max-content auto;
  grid-template-columns: max-content auto;
  padding: 5rem;
  grid-gap: 2rem;
}

.list {
  height: 230px;
  overflow-y: scroll;
}

.grid {
  display: grid;
}

.headers {
  grid-gap: 1rem;
  grid-template-columns: repeat(2, 100px);
  margin: 1.5rem 0rem;
}

.item {
  background: #e5e5e5;
  grid-gap: 1rem;
  grid-template-columns: 100px auto auto;
  border-radius: 15px;
  margin-bottom: 1rem;
  padding: 1rem 0rem;
}

.itemdetail {
  background: #cfcfcf;
  padding: 1rem;
  border-radius: 10px;
}

.itemorder,
.copy {
  display: flex;
  justify-content: center;
  align-items: center;
}

.copy {
  padding-right: 15px;
}

.maindash div {
  width: 100%;
  justify-self: center;
}

.proxylist {
  background-color: #f6f6f6;
  border-radius: 48px;
  padding: 2rem 5rem;
}

.bold {
  font-weight: bold;
}

.box {
  background: #e5e5e5;
  border-radius: 16px;
  padding: 1.3rem;
  max-width: 222px;
}

.purchasetext {
  font-size: 23px;
  font-weight: 700;
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
}

.purchasetext span {
  margin-right: 0.5rem;
}

.purchase {
  margin: 3rem 5rem;
  padding: 3rem;
  background: #f6f6f6;
  scroll-snap-align: start;
  border-radius: 48px;
}

.date {
  font-size: 20px;
  font-weight: 700;
  margin-right: 10px;
}

.ready {
  font-size: 12px;
}

.status-0 {
  color: #cd2b2b;
}

.status-1 {
  color: #2bcd95;
}

.status-2 {
  color: #9265e9;
}

.amount {
  font-size: 15px;
  font-weight: 700;
  color: #ffffff;
  padding: 8px 14px;
  border-radius: 7px;
  background: #2bcd95;
  margin-right: 10px;
}

.name {
  font-size: 15px;
}

.flex {
  display: flex;
  align-items: center;
  margin-bottom: 1rem;
}

.userimg {
  border-radius: 100%;
  grid-area: userimg;
}

.userimg img {
  height: 100%;
  width: auto;
  border-radius: 50%;
}

.userdetails img {
  cursor: pointer;
}

.navlinks a {
  background-color: #7b7b7b;
  color: #ffffff !important;
  padding: 0.55rem;
  border-radius: 10px;
  margin-right: 1rem;
}

.navactive {
  background-color: #2bcd95 !important;
}

.username {
  margin-right: 1rem;
  font-size: 26px;
  grid-area: username;
}

.navlinks {
  display: flex;
  flex-wrap: wrap;
}

.userdetails {
  margin-bottom: 0.2rem;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-right: 1rem;
}

.navgrid {
  margin-right: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 0.5rem;
}

.postloginnav {
  padding: 3rem 3rem;
  justify-content: flex-end;
  display: flex;
}

@media (max-width: 720px) {
  .postloginnav {
    flex-direction: column-reverse;
    align-items: center;
    padding: 2rem 1rem;
  }

  .navgrid {
    margin: 0px;
  }

  .userdetails,
  .navgrid {
    justify-content: center;
    margin-bottom: 0.8rem;
  }

  .userdetails {
    margin: 0rem;
  }

  .navlinks {
    justify-content: center;
  }

  .navlinks a {
    margin-top: 0.8rem;
  }

  .navlinks a:nth-child(4) {
    margin-right: 0px;
  }
}

.boxes {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 3rem;
}

.planexpire div {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 10px;
}

.light {
  color: #979797;
  font-weight: 700;
  font-size: 12px;
}

.not {
  color: #cd2b2b;
}

.expirydate,
.ordernumber {
  color: #979797;
  font-size: 12px;
}

.secone {
  font-family: "Montserrat";
  font-size: 18px;
  font-weight: 700;
  letter-spacing: 3px;
}

.secone img {
  width: 70px;
  margin-bottom: 1rem;
}

.footercontainer {
  width: 50%;
  display: grid;
  grid-template-columns: repeat(5, auto);
  margin: auto;
  padding: 2rem 0rem;
}

footer {
  background: #f6f6f6;
}

.secone,
.sectwo,
.secthree,
.secfour,
.secfive {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-self: center;
}

.footercontainer div a {
  font-size: 19px;
  color: #000000;
  font-weight: 700;
  margin-bottom: 5px;
}

.footercontainer .footerheader {
  margin-bottom: 1rem;
}

.footercontainer .footerlinks {
  font-size: 13px;
  font-weight: 400;
  color: #979797;
}

.secfive img {
  margin-bottom: 10px;
}

@media (min-width: 1920px) {
  .remaining {
    font-size: 48px;
  }

  .smtext {
    font-weight: 700;
    font-size: 10px;
    letter-spacing: 3px;
  }

  .proxylist {
    padding: 3rem;
  }

  .purchase {
    margin: 3rem 10rem;
    padding: 5rem;
  }

  .maindash {
    padding: 5rem 10rem;
  }
}

@media (max-width: 1366px) {
  .boxes {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (max-width: 1200px) {
  .boxes {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (max-width: 1000px) {
  .boxes {
    grid-template-columns: repeat(2, 1fr);
  }

  .box {
    justify-self: center;
  }

  .maindash {
    display: none;
  }

  .footercontainer {
    grid-template-columns: repeat(1, 1fr);
  }

  .footercontainer .footerheader,
  .secfive {
    margin-bottom: 0rem;
    margin-top: 1rem;
  }

  .box {
    box-shadow: 0px 0px 6px rgba(0, 0, 0, 0.16) !important;
  }

  .footercontainer .footerheader {
    font-size: 1.5rem;
  }

  .footercontainer .footerlinks {
    font-size: 1rem;
  }
}

@media (max-width: 768px) {
  .boxes {
    grid-template-columns: repeat(1, 1fr);
  }

  .purchase {
    margin: 5rem 1rem;
  }

  .header {
    margin: 3rem 1rem;
    font-size: 50px;
  }
}

